<template>
  <div style="background:#f1f1f1">
    <el-card v-for="item in orderList.slice(0, 1)" :key="item.id" class="mt-1" shadow="always">
      <div class="orderItem_ground">
        <div v-for="orderItem in orderFirstItem" :key="orderItem.id" class="orderItem">
          <el-image class="orderItem_img" :src="orderItem.prodUrl" />
          <p>{{ orderItem.prodName }}</p>
        </div>
      </div>
      <p class="overflow">{{ item.orderCode }}</p>
      <p class="overflow orderFirs_time">"{{ item.createTime }}"</p>
    </el-card>
    <el-card v-for="item in orderList.slice(-9)" :key="item.id" class="mt-1" shadow="hover">
      <el-row class="order_item" :gutter="20">
        <el-col class="overflow" :span="6">{{ item.orderCode }}</el-col>
        <el-col class="overflow" :span="3">{{ item.prodName }}</el-col>
        <el-col class="overflow" :span="6">{{ item.createTime }}</el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'orderScreen',

  data () {
    return {
      orderList: [], // 订单列表
      orderFirstItem: {}, // 第一个订单的明细
    }
  },

  async created () {
    await this.apiGetOrderList()
    await this.apiGetOrderItem()
    await setInterval(async () => {
      await this.apiGetOrderList()
      await this.apiGetOrderItem()
    }, 20000)
  },

  methods: {
    // 私有方法-----------------------------------------------------------
    // 获取订单列表
    async apiGetOrderList () {
      const data = {
        pageNo: 1,
      }
      // 模糊查询
      const res = await this.$http.post('/woho/order/head/query', data)
      this.orderList = res.data.data.list
    },
    // 获取订单明细列表
    async apiGetOrderItem () {
      const data = {
        pageSize: 10000,
        orderHeaderId: this.orderList[0].id
      }
      const res = await this.$http.post('/woho/order/item/query', data)
      this.orderFirstItem = res.data.data.list
    },
  }
}
</script>

<style lang="scss" scoped>
  .orderItem_ground{
    display: flex;
    .orderItem{
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 200px;
      .orderItem_img{
        width: 200px;
        height: 200px;
      }
    }
  }
  .orderFirs_time{
    font-size: 14px;
    color: #999999;
  }
  .order_item{
    font-size: 14px;
    display: flex;
    .overflow{
      word-wrap: break-word;
      white-space: normal;
      word-break:break-all;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
    }
  }
</style>
